import React, {Component} from 'react'
import PropTypes from 'prop-types'
import {withStyles} from '@material-ui/core'
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardMedia from '@material-ui/core/CardMedia'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import red from '@material-ui/core/colors/red'
import Button from '@material-ui/core/Button'
import {Link} from 'react-router-dom'
import {inject, observer} from "mobx-react/index";

const styles = theme => ({
    root: {
        paddingTop: theme.spacing.unit * 2,
        paddingRight: theme.spacing.unit * 2,
        paddingLeft: theme.spacing.unit * 2,
        paddingBottom: theme.spacing.unit * 2,
    },
    card: {
        maxWidth: 300,
        //paddingTop: theme.spacing.unit * 2 
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    actions: {
        display: 'flex',
    },
    expand: {
        transform: 'rotate(0deg)',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
        marginLeft: 'auto',
        [theme.breakpoints.up('sm')]: {
            marginRight: -8,
        },
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
});

@inject('store')
@observer

class WfCard extends Component {
    /* static propTypes = {
         prop: PropTypes
     }*/
    state = {
        expanded: false,
        data: []//
    };

    constructor(props) {
        super();
        this.store = props.store.DataList;//
        this.data = this.store.data;
    }

    componentDidMount() {
        if (this.store.data.calculate !== undefined) {
            this.setState({//
                //data: this.data.dataGather//
                data: this.store.data.calculate//
            });
        } else {
            setTimeout(() => {//mobx 
                console.log("");
                if (this.store.data.calculate !== undefined) {
                    this.setState({//
                        //data: this.data.dataGather//
                        data: this.store.data.calculate//
                    });
                    console.log(this.store.data.calculate);
                } else {
                    alert("")
                }
            }, 100)
        }
    }


    handleExpandClick = () => {
        this.setState(state => ({
            expanded: !state.expanded
        }));
    };

    render() {
        const {classes, data, match} = this.props;
        console.log("++++++++++++++");
        console.log(data.calculateId);
        let path = `/pji/${match.params.projectId}/ccp/${data.calculateId}`;// 
        return (
            <Button className={classes.root} component={Link} to={path}>{/**/}
                <Card className={classes.card}>
                    <CardHeader
                        title={
                            <Typography variant="h6">
                                {data.name}
                            </Typography>
                        }
                        subheader={
                            <div>
                                <Typography component="p" color="textSecondary">
                                    {data.data}
                                </Typography>
                                <Typography component="p" color="textSecondary">
                                    {data.category}
                                </Typography>
                            </div>
                        }
                    />
                    <CardMedia
                        className={classes.media}
                        image={data.img}
                        title="Paella dish"
                    />
                    <CardContent>
                        <Typography component="p">
                            {data.describe}
                        </Typography>
                    </CardContent>
                </Card>

            </Button>
        )
    }
}

WfCard.propTypes = {
    classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(WfCard)